



























































































































import { Component, Vue } from "vue-property-decorator";
import { mapGetters, mapState } from "vuex";
import {
  BAlert,
  BButton,
  BCollapse,
  BInputGroup,
  BNavItem,
  BNavbar,
  BNavbarNav,
  BNavbarToggle,
  BSpinner,
  VBModal,
  VBTooltip,
} from "bootstrap-vue";
import Settings from "@/components/Settings.vue";

@Component({
  components: {
    Settings,
    "b-alert": BAlert,
    "b-button": BButton,
    "b-collapse": BCollapse,
    "b-input-group": BInputGroup,
    "b-navbar": BNavbar,
    "b-nav-item": BNavItem,
    "b-navbar-nav": BNavbarNav,
    "b-navbar-toggle": BNavbarToggle,
    "b-spinner": BSpinner,
  },
  directives: {
    "b-modal": VBModal,
    "b-tooltip": VBTooltip,
  },
  computed: {
    ...mapGetters(["shouldShowAlert", "warningMessage"]),
    ...mapState("schedule", ["wasmLoaded"]),
    updateAvailable: {
      get() {
        return this.$store.state.updateAvailable;
      },
      set() {
        this.$store.commit("toggleUpdateNotice", false);
      },
    },
  },
})
export default class App extends Vue {
  searchCallback: number | null = null;
  searching = false;

  search(input: string, searchTimeout = 250) {
    this.searching = true;

    if (this.searchCallback !== null) {
      clearTimeout(this.searchCallback as number);
    }

    if (input.length === 0) {
      this.searching = false;
      this.$router.push("/").catch(() => {
        return;
      });
    } else {
      this.searchCallback = setTimeout(() => {
        this.$router.push("/search?" + input).catch(() => {
          this.searching = false;
          return;
        });
        this.searching = false;
      }, searchTimeout);
    }
  }

  reloadPage() {
    window.location.reload(true);
  }
}
